$posX: random(60) + 40;
@function stam() {
  @return random(100);
}

.control-panel {
  position: absolute;
  align-self: flex-end;
  display: flex;
  justify-content: start;
  align-items: flex-end;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  .reactions-container {
    position: relative;
    top: -120px;
    left: -10px;
  }
  .reactions {
    margin: 10px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: darkgray;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    pointer-events: auto;
    transition: 0.3s;

    &:hover {
      color: maroon;
      cursor: pointer;
      background: radial-gradient(
        circle,
        rgba(238, 174, 202, 1) 0%,
        rgba(89, 89, 89, 1) 100%
      );
    }
  }

  .volume {
    margin: 10px 0;
    position: relative;
    color: darkgray;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    transition: 0.3s;

    #volume {
      background: maroon;
      position: absolute;
      transform: rotate(270deg);
      margin: 0 auto;
      top: -75px;
      left: -50px;
    }

    &:hover {
      color: maroon;
      cursor: pointer;
      background: radial-gradient(
        circle,
        rgba(238, 174, 202, 1) 0%,
        rgba(89, 89, 89, 1) 100%
      );
    }
  }
  .like {
    background: url('../../img/fb-like.png') center/cover no-repeat;
  }

  .heart {
    background: url('../../img/fb-heart.png') center/cover no-repeat;
  }

  .wow {
    background: url('../../img/fb-wow.jpg') center/cover no-repeat;
  }

  .sad {
    background: url('../../img/fb-sad.png') center/cover no-repeat;
  }

  .lol {
    background: url('../../img/fb-lol.png') center/cover no-repeat;
  }

  .angry {
    background: url('../../img/fb-angry.png') center/cover no-repeat;
  }

  .reaction-animation {
    $posX: attr(left);
    position: absolute;
    top: max;
    // left: 40%;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    transition: 0.5s;
    animation-duration: 2s;
    animation-name: example;
    animation-iteration-count: infinite;
    z-index: random(900);
    @keyframes example {
      0% {
        top: calc(100% - 50px);
        opacity: 30%;
        animation-timing-function: ease-in;
      }
      50% {
        top: calc(100% - 150px);
        opacity: 70%;
        animation-timing-function: ease-out;
      }
      100% {
        top: calc(100% - 250px);
        opacity: 0%;
      }
    }
  }

  .hidden {
    display: none;
  }
}
